
import {
    UPDATE_PRODUCTS,
    UPDATE_CATEGORIES,
    UPDATE_CURRENT_CATEGORY,
    ADD_TO_CART,
    ADD_MULTIPLE_TO_CART,
    REMOVE_FROM_CART,
    UPDATE_CART_QUANTITY,
    CLEAR_CART,
    TOGGLE_CART
  } from '../../utils/actions';


const initialState ={
    products: [],
        cart: [],
        cartOpen: false,
        categories: [],
        currentCategory: ''
}

const reducer = (previousState = initialState, action)=> {
    switch (action.type) {
        // if action type value is the value of `UPDATE_PRODUCTS`, return a new state object with an updated products array
        case UPDATE_PRODUCTS:
          return {
            ...previousState,
            products: [...action.products]
          };
        // if action type value is the value of `UPDATE_CATEGORIES`, return a new state object with an updated categories array
        case UPDATE_CATEGORIES:
          return {
            ...previousState,
            categories: [...action.categories]
          };
        case UPDATE_CURRENT_CATEGORY:
          return {
            ...previousState,
            currentCategory: action.currentCategory
          };
        case ADD_TO_CART:
          return {
            ...previousState,
            cartOpen: true,
            cart: [...previousState.cart, action.product]
          };
        case ADD_MULTIPLE_TO_CART:
          return {
            ...previousState,
            cart: [...previousState.cart, ...action.products],
          };
        case REMOVE_FROM_CART:
          let newPreviousState = previousState.cart.filter(product => {
            return product._id !== action._id;
          });
    
          return {
            ...previousState,
            cartOpen: newPreviousState.length > 0,
            cart: newPreviousState
          };
    
        case UPDATE_CART_QUANTITY:
          return {
            ...previousState,
            cartOpen: true,
            cart: previousState.cart.map(product => {
              if (action._id === product._id) {
                product.purchaseQuantity = action.purchaseQuantity;
              }
              return product;
            })
          };
        case CLEAR_CART:
          return {
            ...previousState,
            cartOpen: false,
            cart: []
          };
        case TOGGLE_CART:
          return {
            ...previousState,
            cartOpen: !previousState.cartOpen
          };
    
        default:
          return previousState;
      }

}

export default reducer;